body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  box-sizing: border-box;
  min-height: 100%; }

#root {
  height: 100%;
  position: relative; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.main-header {
  height: 70px;
  margin-bottom: 0 !important; }
  .main-header__logo {
    width: 100px; }
  .main-header__sign-out > .icon {
    font-size: 32px;
    cursor: pointer; }

.user-circle {
  padding: 20px !important;
  cursor: pointer; }
  .user-circle__title {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 16px !important;
    line-height: 30px !important; }

.body-type__image {
  margin-left: 3px !important; }

.add-car-modal__content .engine-input {
  max-width: 200px; }

.empty-list-container {
  width: 100%; }

.card-list-wrapper {
  padding-bottom: 70px; }

.add-car-container {
  margin-top: 15px !important; }

.card-list-container {
  min-height: 260px; }

.loader-center {
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.login-form {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .login-form__container {
    width: 320px !important; }
  .login-form__title {
    font-size: 20px !important; }

.details-table-wrapper .details-table__header__row {
  font-size: 16px; }
  .details-table-wrapper .details-table__header__row > .cell {
    padding: 5px; }

.kilometrage-wrapper .kilometrage-container__btn button {
  margin-top: 20px !important; }

.kilometrage-wrapper .error-container {
  margin-right: 15px; }

.description-container {
  margin-top: 20px !important; }

.error-wrapper {
  background-color: #00B5AD;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%; }
  .error-wrapper__title {
    font-size: 120px; }
  .error-wrapper__link {
    font-size: 16px;
    color: #fff; }

.app-container {
  min-height: calc(100% - 70px);
  position: relative;
  display: flex; }

